import Model from "../model/weatherModel";
import View from "../view/weatherView";

class WeatherController {
  weatherView: View;
  weatherModel: Model;
  constructor(weatherView: View, weatherModel: Model) {
    this.weatherView = weatherView;
    this.weatherModel = weatherModel;

    this.weatherModel.bindWeatherChanged(this.onWeatherUpdate);
  }

  onWeatherUpdate = (temperature: string, place: string) => {
    this.weatherView.updateWeatherTxt(temperature, place);
  };
}

export default WeatherController;
