import { WEATHER_KEY } from "../../config";

class WeatherModel {
  private COORDS_STORAGE: string;
  onWeatherUpdate!: Function;
  private baseUrl: string;
  private axiosHelper: AxiosHelper;

  constructor() {
    this.COORDS_STORAGE = "coords";
    this.baseUrl = "https://api.openweathermap.org/data/2.5";

    this.loadCoords();
  }

  bindWeatherChanged = (callback: Function) => {
    this.onWeatherUpdate = callback;
  };

  saveCoords = (coordsObj: Object) => {
    localStorage.setItem(this.COORDS_STORAGE, JSON.stringify(coordsObj));
  };

  handleGeoSuccess = (position: Position): void => {
    const latitude = position.coords.latitude;
    const longitude = position.coords.longitude;

    const coordsObj = {
      latitude,
      longitude
    };

    this.saveCoords(coordsObj);
    this.getWeather(latitude, longitude);
  };

  handleGeoError = () => {
    console.log("geo error");
  };

  askCoords = () => {
    navigator.geolocation.getCurrentPosition(
      this.handleGeoSuccess,
      this.handleGeoError
    );
  };

  loadCoords = () => {
    const loadedCoords = localStorage.getItem(this.COORDS_STORAGE);

    if (loadedCoords === null) {
      this.askCoords();
    } else {
      const parseCoords = JSON.parse(loadedCoords);
      console.log(parseCoords);
      this.getWeather(parseCoords.latitude, parseCoords.longitude);
    }
  };

  getWeather = (lat: number, lon: number) => {
    fetch(
      `${this.baseUrl}/weather?lat=${lat}&lon=${lon}&appid=${WEATHER_KEY}&units=metric`
    )
      .then(response => {
        return response.json();
      })
      .then(json => {
        const temperature = json.main.temp;
        const place = json.name;

        console.log(temperature, place);
        this.onWeatherUpdate(temperature, place);
      });
  };
}

interface Position {
  readonly coords: Coordinates;
  readonly timestamp: number;
}

export default WeatherModel;
